"use client";

import "gantt-task-react/dist/index.css";

import { useState } from "react";
import dynamic from "next/dynamic";
import { Alert, Card, Segmented, Space, Typography } from "antd";
import type { Task } from "gantt-task-react";
import { ViewMode } from "gantt-task-react";

const GanttChart = dynamic(() => import("gantt-task-react").then((mod) => mod.Gantt), { ssr: false });

const seedTasks: Task[] = [
  {
    id: "G-01",
    name: "Discovery and architecture",
    start: new Date(2026, 1, 24),
    end: new Date(2026, 1, 27),
    type: "task",
    progress: 90,
    isDisabled: false,
    styles: { progressColor: "#1677ff", progressSelectedColor: "#0958d9" },
  },
  {
    id: "G-02",
    name: "Template library integration",
    start: new Date(2026, 1, 27),
    end: new Date(2026, 2, 3),
    type: "task",
    progress: 45,
    dependencies: ["G-01"],
    isDisabled: false,
    styles: { progressColor: "#13c2c2", progressSelectedColor: "#08979c" },
  },
  {
    id: "G-03",
    name: "Playwright and QA hardening",
    start: new Date(2026, 2, 3),
    end: new Date(2026, 2, 6),
    type: "task",
    progress: 20,
    dependencies: ["G-02"],
    isDisabled: false,
    styles: { progressColor: "#fa8c16", progressSelectedColor: "#d46b08" },
  },
  {
    id: "G-04",
    name: "Release candidate",
    start: new Date(2026, 2, 6),
    end: new Date(2026, 2, 7),
    type: "milestone",
    progress: 0,
    dependencies: ["G-03"],
    isDisabled: false,
    styles: { progressColor: "#722ed1", progressSelectedColor: "#531dab" },
  },
];

const VIEW_OPTIONS = [
  { label: "Day", value: ViewMode.Day },
  { label: "Week", value: ViewMode.Week },
  { label: "Month", value: ViewMode.Month },
] as const;

export default function GanttPage() {
  const [tasks, setTasks] = useState(seedTasks);
  const [viewMode, setViewMode] = useState<ViewMode>(ViewMode.Week);

  return (
    <div className="p-6 space-y-4">
      <Space orientation="vertical" size={4}>
        <Typography.Title level={2} className="!mb-0">Gantt Template</Typography.Title>
        <Typography.Text type="secondary">
          Powered by `gantt-task-react` with drag-to-reschedule and progress editing.
        </Typography.Text>
      </Space>

      <Card>
        <Space orientation="vertical" className="w-full" size={16}>
          <Segmented
            value={viewMode}
            options={VIEW_OPTIONS.map((option) => ({ label: option.label, value: option.value }))}
            onChange={(value) => setViewMode(value as ViewMode)}
          />
          <Alert
            showIcon
            type="info"
            title="Interactive Gantt"
            description="Drag a bar edge to change dates or drag progress to update completion."
          />
          <div className="overflow-auto border border-token-border-default rounded-md bg-white">
            <GanttChart
              tasks={tasks}
              viewMode={viewMode}
              listCellWidth="210px"
              columnWidth={viewMode === ViewMode.Day ? 60 : 90}
              onDateChange={(task) => {
                setTasks((prev) => prev.map((existing) => (existing.id === task.id ? task : existing)));
                return true;
              }}
              onProgressChange={(task) => {
                setTasks((prev) => prev.map((existing) => (existing.id === task.id ? task : existing)));
                return true;
              }}
              onDelete={(task) => {
                setTasks((prev) => prev.filter((existing) => existing.id !== task.id));
                return true;
              }}
            />
          </div>
        </Space>
      </Card>
    </div>
  );
}
